.contact-wrapper {
    margin-top: 120px;

    @media (max-width: 767px) {
        .contact-wrapper {
            margin-top: 75px;
        }
    }

    .contact {
        background-color: #2879fe;
        height: 60px;
        color: white;
        @media (max-width: 767px) {
            height: auto;
        }
        .wrapper {
            width: 100%;
            height: 100%;
            padding: 15px;
            max-width: 1440px;
            margin: 10px auto;
            display: flex;
            justify-content: space-between;
            align-items: center;
            @media (max-width: 767px) {
                flex-direction: column;
                gap: 25px;
            }
            .left {
                h2 {
                    text-transform: uppercase;
                }
            }
            .center {
                display: flex;
                align-items: center;
                input {
                    padding: 10px;
                    border: none;
                    border-radius: 5px 0 0 5px;
                }
                button {
                    padding: 10px;
                    border: none;
                    border-radius: 0 5px 5px 0;
                    color: white;
                    background-color: gray;
                }
            }
            .right {
                display: flex;
                gap: 10px;
            }
        }
    }
}
