.register-wrapper {
    margin-top: 120px;
    width: 100%;
    max-width: 1440px;
    margin-right: auto;
    margin-left: auto;
    padding: 10px;
    flex: 0 0 80%;
    position: relative;
    background-image: url('../../assets/hero-home-m.svg');
    background-position: left -19rem center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;

    .form {
        position: relative;
        z-index: 2;
        width: 30%;
        padding: 20px;
        background-color: white;

        h1 {
            color: #054d56;
            font-size: 1.5rem;
            font-family: 'Neo Sans Std';
            text-transform: uppercase;
            font-weight: 500;
        }
        .form-wrapper {
            display: flex;
            flex-direction: column;

            input {
                flex: 1;
                min-width: 40%;
                margin: 10px 0px 10px 0px;
                padding: 10px;
            }
        }
        .form-links {
            margin: 5px 0;
            font-size: .7rem;
            text-decoration: underline;
            cursor: pointer;
        }
        button {
            border: none;
            width: 40%;
            padding: 15px 20px;
            background-color: #054d56;
            color: white;
            cursor: pointer;
            margin-bottom: 10px;
        }
        button:disabled {
            background-color: #035660;
            cursor: not-allowed;
        }
        span {
            color: red;
        }
    }
}
.register-wrapper::after {
    content: '';
    background: linear-gradient(to bottom, rgba(3, 86, 96, 0), 50%, #035660 100%);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: bottom center;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
}

@media (max-width: 767px) {
    .register-wrapper {
        margin-top: 75px;
    }
}
