.register-wrapper {
    margin-top: 120px;
    width: 100%;
    max-width: 1440px;
    margin-right: auto;
    margin-left: auto;
    padding: 10px;
    flex: 0 0 80%;
    position: relative;
    background-image: url('../../assets/hero-home-m.svg');
    background-position: left -19rem center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;

    .form {
        position: relative;
        z-index: 2;
        width: 40%;
        padding: 20px;
        background-color: white;

        h1 {
            color: #054d56;
            font-size: 1.5rem;
            font-family: 'Neo Sans Std';
            text-transform: uppercase;
            font-weight: 500;
        }
        .form-wrapper {
            display: flex;
            flex-wrap: wrap;

            input {
                flex: 1;
                min-width: 40%;
                margin: 20px 10px 0 0;
                padding: 10px;
            }
        }
        .agreement {
            font-size: 12px;
            margin: 20px 0;
            line-height: 1.5;
        }
        button {
            border: none;
            width: 40%;
            padding: 15px 20px;
            background-color: #054d56;
            color: white;
            
            cursor: pointer;
        }
    }
}
.register-wrapper::after {
    content: '';
    background: linear-gradient(to bottom, rgba(3, 86, 96, 0), 50%, #035660 100%);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: bottom center;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
}

@media (max-width: 767px) {
    .register-wrapper {
        margin-top: 75px;
    }
}
