.item {
    display: block;
    position: relative;
    height: 100%;
}
.item__logo {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(5, 77, 86, 0.6);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.8s, visibility 0.8s linear 0.1s;
}
.item__logo img {
    width: 180px;
    height: 180px;
}
.item__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.item:hover .item__logo {
    visibility: visible;
    opacity: 1;
}

.shop__body {
    display: flex;
    overflow: hidden;
}
.shop__column {
    flex: 0 1 16.666%;
}
.shop__column_b {
    flex: 0 1 33.333%;
}
.block-shop {
    height: 100%;
}
.block-shop__row {
    display: flex;
    height: 50%;
}
.block-shop__row_bottom {
    height: 50%;
}
.shop__item {
    padding-top: 200%;
}
.block-shop__column {
    flex: 0 1 50%;
    height: 100%;
}

.shop__footer {
    margin-top: 2px;
    padding: 75px;
    background-color: #1c6c78;
    text-align: center;
}
.shop__btn {
    border: 2px solid #52d2e3;
    padding: 10px 15px 10px 15px;
    color: #f2f1eb;
    font-size: 1rem;
    font-family: 'Neo Sans Std';
    text-transform: uppercase;
    font-weight: 400;
    cursor: pointer;
}

@media (max-width: 767px) {
    .shop__body {
        flex-wrap: wrap;
    }
    .shop__column {
        flex: 1 1 50%;
    }
    .shop__item {
        padding-top: 80%;
    }
    .shop__btn {
        font-size: 0.6rem;
    }
    .shop__footer {
        padding: 50px;
    }
}
